import React from 'react'
import './Services.css'
import ScrollAnimation from 'react-animate-on-scroll'
import classNames from 'classnames'

import LogoFace from '../../components/LogoFace'
import LogoRF from '../../components/LogoRF'

import data from '../../data/services'
import MailModal from './MailModal'

const Services = props => {
  const [openModal, setOpenModal] = React.useState(false)

  return (
    <div className='services'>
      <MailModal
        open={openModal}
        close={() => setOpenModal(false)}
      />
      <div className='services__info'>Rejestracja do zabiegów laryngologicznych możliwa jest pod numerem telefonu <span style={{ color: 'rgb(255, 144, 0)' }}>733131384</span> oraz online.</div>

      <div className='services__legend--container'>
        <div className='services__legend'>
          <div className='flex align-center'>
            <LogoFace className='services__legend__logo' />
            <span>Wykonywane na miejscu</span>
          </div>
          <div className='flex-center'>
            <div className='services__legend__line'></div>
          </div>
          <div className='flex align-center'>
            <span className='services__legend__logo' ><i>RF</i></span>
            <span>Metoda termoablacji RF</span>
          </div>
          <div
            className='flex-center services__legend__rejestration'
            onClick={() => setOpenModal(true)}
          >
            Rejestracja do odpłatnych <br />zabiegów laryngologicznych
          </div>
        </div>
      </div>

      <div className='services__list'>
        {data.map(el => (
          <ScrollAnimation
            key={el.text}
            animateIn='fadeInLeft'
            duration={1.5}
            offset={30}
          >
            <div className='services__list__item' >
              {el.iconFace && <LogoFace className='services__logo' />}
              {el.iconRF && <LogoRF className='services__logo services__logo__rf' />}
              <span
                className={classNames('services__list__item__description', { 'services__list__item__description--clickable': el.tipsOnClick })}
                onClick={el.tipsOnClick ? () => props.history.push('porady') : null}
              >
                {el.text}
              </span>
            </div>
          </ScrollAnimation>
        ))}
      </div>
    </div >
  )
}

export default Services