import React from 'react'

const PricesList = props => {
  return (
    <div className='prices__'>
      {props.data.map(el => (
        <div key={el.service} className='prices__item '>
          <div className='prices__item__service card flex-center'>
            {el.service}
            {el.descriptions.map(el => <span key={el}>{el}</span>)}
          </div>
          <div className='prices__item__price card flex-center'>
            {el.prices.map(el => <span key={el}>{el}</span>)}
          </div>
        </div>
      ))}
    </div>
  )
}

export default PricesList