import React from 'react'

import { Dialog, TextField, CircularProgress, Collapse } from '@material-ui/core'
import CheckIcon from '@material-ui/icons/Check'

const initialState = {
  mailTo: 'zabiegi@naszlaryngolog.com',
  inputs: {
    mail: '',
    title: '',
    message: ''
  },
  errors: {},
  loading: false,
  sent: false
}

class MailModal extends React.Component {
  state = initialState

  setInput = input => evt => {
    this.setState({
      inputs: {
        ...this.state.inputs,
        [input]: evt.target.value
      }
    },
      () => {
        if (Object.values(this.state.errors).includes(true)) {
          this.checkInputs()
        }
      }
    )
  }

  checkInputs = () => {
    const errors = {
      mail: !this.state.inputs.mail.match(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/),
      title: this.state.inputs.title.length < 3,
      message: this.state.inputs.message.length < 10,
    }

    this.setState({ errors })
    return Object.values(errors).includes(true)
  }

  send = () => {
    if (this.checkInputs()) return
    const data = {
      mailTo: this.state.mailTo,
      mailFrom: this.state.inputs.mail,
      title: this.state.inputs.title,
      message: this.state.inputs.message
    }

    console.log(data)
    this.setState({ loading: true })
    setTimeout(() => this.setState({ sent: true }), 1000)
  }

  close = () => {
    this.props.close()
    setTimeout(() => this.setState(initialState), 300)
  }

  render() {
    return (
      <Dialog
        open={this.props.open}
        onClose={this.close}
        PaperProps={{ style: { padding: 30, width: 400, display: 'flex', flexDirection: 'column', alignItems: 'center' } }}
      >
        {this.state.loading ?
          <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <CircularProgress size={100} style={{ color: 'rgb(209, 216, 73', opacity: this.state.sent ? 0 : 1, position: 'absolute' }} />
            <div style={{ backgroundColor: 'rgb(209, 216, 73)', width: 100, height: 100, borderRadius: 50, display: 'flex', justifyContent: 'center', alignItems: 'center', opacity: this.state.sent ? 1 : 0, transition: '0.5s' }}>
              <CheckIcon style={{ color: 'white', fontSize: '5rem' }} />
            </div>
            <Collapse in={this.state.sent} >
              <h2 style={{ color: '#333', fontSize: '2.5rem', marginTop: 10, }}>Wiadomość wysłana</h2>
              <div style={{ height: 20 }}>
                <div className='mail-modal__button flex-center'
                  onClick={this.close}
                >
                  Wróć
                </div>
              </div>
            </Collapse>
          </div>
          :
          <React.Fragment>
            <TextField
              fullWidth
              variant='outlined'
              InputProps={{ style: { fontSize: 16, backgroundColor: 'rgb(235, 235, 228)' } }}
              style={{ marginBottom: 20, width: 300 }}
              value={this.state.mailTo}
              disabled
            />
            <TextField
              fullWidth
              variant='outlined'
              InputProps={{ style: { fontSize: 16, } }}
              FormHelperTextProps={{ style: { fontSize: 14 } }}
              style={{ marginBottom: 20, width: 300 }}
              placeholder='Twój adres email'
              value={this.state.inputs.mail}
              onChange={this.setInput('mail')}
              error={this.state.errors.mail}
              helperText={this.state.errors.mail && 'Wprowadź prawidłowy adres email'}
            />
            <TextField
              fullWidth
              variant='outlined'
              InputProps={{ style: { fontSize: 16 } }}
              FormHelperTextProps={{ style: { fontSize: 14 } }}
              style={{ marginBottom: 20, width: 300 }}
              placeholder='Temat'
              value={this.state.inputs.title}
              onChange={this.setInput('title')}
              error={this.state.errors.title}
              helperText={this.state.errors.title && 'Minimum 3 znaki'}
            />
            <TextField
              fullWidth
              variant='outlined'
              InputProps={{ style: { fontSize: 16 } }}
              FormHelperTextProps={{ style: { fontSize: 14 } }}
              style={{ marginBottom: 20 }}
              placeholder='Wiadomość'
              multiline
              rows={6}
              value={this.state.inputs.message}
              onChange={this.setInput('message')}
              error={this.state.errors.message}
              helperText={this.state.errors.message && 'Minimum 10 znaków'}
            />
            <div style={{ display: 'flex', justifyContent: 'space-around', width: '100%' }}>
              <div className='mail-modal__button flex-center'
                onClick={this.send}
              >
                Wyślij
              </div>
              <div className='mail-modal__button flex-center mail-modal__button--close'
                onClick={this.close}
              >
                Wróc
              </div>
            </div>
          </React.Fragment>
        }
      </Dialog>
    )
  }
}

export default MailModal