import React from 'react'
import './Dashboard.css'

import doctors from '../../data/doctors'
import DoctorButton from '../../components/DoctorButton'
import DefaultContent from './DefaultContent'
import News from '../News'

class Dashboard extends React.Component {
  state = {
    currentDoctorId: null
  }

  setDoctor = currentDoctorId => this.setState({ currentDoctorId })

  componentDidUpdate(prevProps) {
    if (prevProps.location !== this.props.location) {
      this.setDoctor(null)
    }
  }

  render() {
    return (
      <div className='dashboard'>
        <div className='dashboard__doctors'>
          <h3 className='dashboard__doctors__title flex-center'>Nasi Specjaliści:</h3>
          <div className='dashboard__doctors_buttons'>
            {doctors.map(el => (
              <DoctorButton
                key={el.id}
                active={el.id === this.state.currentDoctorId}
                onClick={() => this.setDoctor(el.id)}
              >
                {el.name}
              </DoctorButton>
            ))}
          </div>

        </div>
        <div className='dashboard__content-container'>
          <div className='dashboard__content'>
            {this.state.currentDoctorId === null ?
              this.props.location.pathname === '/aktualnosci' ?
                <News />
                :
                <DefaultContent className='dashboard__content--default' />
              :
              <div
                dangerouslySetInnerHTML={{ __html: doctors.find(el => el.id === this.state.currentDoctorId).description }}
              />
            }
          </div>
        </div>
      </div>
    )
  }
}

export default Dashboard