import React from 'react'

import background1 from '../assets/background1.jpg'
import background2 from '../assets/background2.jpg'
import background3 from '../assets/background3.jpg'
import './components.css'

const backgrounds = [background1, background2, background3]

class Background extends React.Component {
  state = {
    current: 0
  }

  componentDidMount() {
    this.interval = setInterval(this.changeBackground, 10000)
  }

  componentWillUnmount() {
    clearInterval(this.interval)
  }

  changeBackground = () => {
    const nextBackground = this.state.current + 1
    this.setState({
      current: nextBackground < backgrounds.length ? nextBackground : 0
    })
  }

  render() {
    return (
      <div className='background'
        style={{
          backgroundImage: 'url("' + backgrounds[this.state.current] + '")'
        }}
      />

    )
  }
}

export default Background